import { userUpdatePayload } from '../redux/features/user/types';
import { logger } from '../utils';
import client from './configAxios';
import { updateProfile } from './endpoint';

export const fetchUpdateUserProfile = async (
  accountId: string,
  payload: userUpdatePayload,
) => {
  try {
    const formData = new FormData();
    formData.append('AccountId', accountId);
    formData.append('FirstName', payload.FirstName?.trim() || '');
    formData.append('LastName', payload.LastName?.trim() || '');
    if (payload.PhotoImage) {
      formData.append('PhotoImage', payload.PhotoImage);
    }

    const response = await client.put(updateProfile, formData, {
      headers: {
        'Content-Type': 'multipart/form-data',
      },
    });
    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error updateUser', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
    };
  }
};
